<template>
	<view>
		<uni-nav-bar shadow title="录播" color="#ffffff" background-color="#00aaff" :fixed="true" :statusBar="true"></uni-nav-bar>
		<view v-show="show" class="main">
			<canvas class="SmartScreen" canvas-id="SmartScreen"  :style="canvasStyle"> </canvas>
		</view>
		
	</view>
</template>

<script>
	import {liveFifo} from '@/static/utils/FIFO'
	import {cacheFifo} from '@/static/utils/CacheFifo'
	let live = new liveFifo()
	let cache = new cacheFifo()
	let ctx = uni.createCanvasContext("SmartScreen",this)
	let points = []
	export default {
		data() {
			return {
				ctx:'',
				nowPenColor:'#000000',
				canvasStyle:{},
				w:0,
				h:0,
				show:false,
				canvasHeight:0,
				canvasWidth:0,
				number:0,
				flge:false,
				timer:0,
				CurrentTime:0,
				totalTime:0
			}
		},
		async onLoad(param) {
			uni.showLoading({
				title:'组件加载中'
			})
			await this.$refresh(param)
			this.setPenStyle()
			this.setCanvas()
			uni.connectSocket({
				url:getApp().globalData.ws
			})
			
			this.live()
		},
		onShow() {
			
		},
		onUnload() {
			this.flge = false
			live.clear()
			console.log("onUnLoad");
			console.log(cache.len());
			this.setNowPenColor('#000000')
			this.sync(0)
		},
		onHide() {
			// this.flge = false
			
		},
		onReady(){
			let that = this
			uni.onSocketMessage(function(res){
				let data = Array.prototype.map.call(new Uint8Array(res.data), x => ('00' + x.toString(16)).slice(-2)).join('')
				// console.log("接收数量:"+(that.number++)+"	数据:"+data);
				// this.number++;
				let type = 0
				let x = 0
				let y = 0
				let timeStamp = 0
				if(data.length>10){
					let xStr = data.substr(2,4)
					let yStr = data.substr(6,4)
					// console.log("xStr:"+xStr+"yStr:"+yStr);
					let timeStampStr = data.substr(10,data.length)
					x = that.$hexStrToDEC(xStr)
					y = that.$hexStrToDEC(yStr)
					timeStamp = that.$hexStrToDEC(timeStampStr)
				
					let xx = (that.canvasHeight/getApp().globalData.boardInfo.maximumX)*x;
					
					let yy = (that.canvasWidth/getApp().globalData.boardInfo.maximumY)*y;
					
					x = yy;
					x = that.canvasWidth - x;
					y = xx;
					
					let typeStr = data.substr(0,2)
					type = that.$hexStrToDEC(typeStr)
					that.setFifo(type,x,y,timeStamp)
				}else{
					let timeStampStr = data.substr(2,data.length)
					timeStamp = that.$hexStrToDEC(timeStampStr)
					let typeStr = data.substr(0,2)
					type = that.$hexStrToDEC(typeStr)
					that.setFifo(type,null,null,timeStamp)
				}
			});
			
		},	
		methods: {
			setPenStyle(){
				ctx.lineWidth = 1;
				ctx.setStrokeStyle(this.nowPenColor)
				ctx.lineCap = "round"
				ctx.lineJoin = "round"
			},
			setCanvas(){
				let data = {
					"height":getApp().globalData.webInfo.windowHeight,
					"width":getApp().globalData.webInfo.windowWidth
				}
				this.updateResize(data)
			},
			updateResize(param){
				// console.log(param);
				let resizeWidth = param.width
				let resizeHeight = param.height
				let physProportion = getApp().globalData.boardInfo.physWidth/getApp().globalData.boardInfo.physHeight
				if(resizeWidth/resizeHeight > physProportion){
					this.canvasHeight = resizeHeight
					this.canvasWidth = resizeHeight*physProportion
				}else{
					this.canvasWidth = resizeWidth
					this.canvasHeight = resizeWidth*physProportion
				}
				this.canvasStyle = "width: "+this.canvasWidth+"px; height: "+this.canvasHeight+"px;"
				uni.hideLoading()
				this.show = true
			},
			setFifo(eventType,x,y,timeStamp,type){
				if(eventType&64){//0100 0000
					eventType&=15 // 0000 1111
					this.installCacheFifo(eventType, x, y, timeStamp, type)
					let start = cache.getIndex(0)
					this.totalTime = parseInt((timeStamp-start.timeStamp)/1000)
					uni.onSocketClose(function (res) {
					  console.log('WebSocket 已关闭！');
					});
					this.sync(0)
				}
				eventType&=15 // 0000 1111
				this.installCacheFifo(eventType, x, y, timeStamp, type)
				
			},
			//插入缓存FIFO
			installCacheFifo(eventType, x, y, timeStamp) {
				cache.set({
					"type": eventType,
					"X": x,
					"Y": y,
					"timeStamp": timeStamp
				})
			},
			sync(index){
				console.log('sync');
				let tmp = cache.getIndex(index)
				this.timer = tmp.timeStamp
				let fifo = cache.getSync(index)
				console.log("sync len:"+fifo.length);
				console.log("sync 第一条数据:");
				console.log(fifo[0]);
				let that = this
				fifo.forEach(function(data){
					that.dataHandler(data)
				})
				let lives = cache.getFifo(index)
				console.log(lives);
				console.log('live len:'+lives.length);
				lives.forEach(function(data){
					live.set(data)
				})
				this.flge = true
			},
			dataHandler(data){
				console.log(data.type);
				console.log('dataHanlder');
				if(data.type == 0){
					ctx.beginPath();
				}else if(data.type == 1){
					points = [];
				}else if(data.type == 5){
					this.setNowPenColor('#ff0000')
				}else if(data.type == 3){
					this.setNowPenColor('#000000')
				}else if(data.type == 4){
					this.setNowPenColor('#00aaff')
				}else if(data.type == 6){
					this.finish()
				}else if(data.type == 7){
					this.clearCanvas()
				}else if(data.type == 2){
					
				}else if(data.type == 10){
					ctx.setFillStyle("#ffffff")
					ctx.setStrokeStyle("#ffffff")
					ctx.arc(data.X, data.Y, 20, 0, 2*Math.PI)
					ctx.fill();
					ctx.stroke()
					ctx.draw(true);
				}else{
					this.setNowPenColor(this.nowPenColor)
					points.push(data);
					let len = points.length;
					if(len>=2){
						this.draw()	
					}
				}	
			},
			clearCanvas(){
				ctx.clearRect(0, 0, this.canvasWidth, this.canvasHeight);
				ctx.draw();
			},
			draw(){
				console.log("draw");
				let point1 = points[0]
				let point2 = points[1]
				points.shift()
				ctx.moveTo(point1.X, point1.Y)
				ctx.lineTo(point2.X, point2.Y)
				ctx.stroke()				
				ctx.draw(true)
			},
			setNowPenColor(nowColor){
				ctx.setFillStyle(nowColor)
				ctx.setStrokeStyle(nowColor)
				this.nowPenColor = nowColor
			},
			live(){
				setInterval(()=>{
					if(this.flge&&live.len()){
						let data = live.get()
						// console.log('timer:'+this.timer);
						// console.log('dataTimeStamp:'+data.timeStamp);
						if(this.timer++ == data.timeStamp){
							console.log('live draw');
							data = live.reget()
							this.CurrentTime++
							this.dataHandler(data)
						}
					}
				},1);
			}
		}
	}
</script>

<style>
	page{
		background-color: #e8e8e8;
		text-align: center;
	}
	.SmartScreen{
		background-color: #ffffff;
		margin: 0 auto;
	}
</style>
